<template>
	<div id="baseSetting-main">
		<section class="activity-type">
			<h4>活动类型</h4>
			<form>
				<ul class="type-radio clear">
					<li class="left">
						<input type="radio" name="type"  id="activity-wheel" checked="true">
						<label for="activity-wheel">大转盘</label>
					</li>
					<li class="left">
						<input type="radio" name="type" id="activity-lottery">
						<label for="activity-lottery">抽奖箱</label>
					</li>
					<li class="left">
						<input type="radio" name="type"  id="activity-scraping">
						<label for="activity-scraping">刮刮乐</label>
					</li>
				</ul>
			</form>
		</section>
		<section class="activity-base">
			<h4>基本设置</h4>
			<ul class="base-container">
				<li class="activity-name">
					<label for="">活动名称:</label>
					<input type="text" name="" placeholder="请输入活动名称">
				</li>
				<li class="activity-begin">
					<label for="">开始时间:</label>
					<input type="text" name="" placeholder="2018-09-24 9:30">
				</li>
				<li class="activity-end">
					<label for="">结束时间:</label>
					<input type="text" name="" placeholder="2018-09-29 9:30">
				</li>
				<li class="activity-introduce clear">
					<label for="">主办方介绍:</label>
					<textarea name="introduce" placeholder="例：广州景行广告策划有限公司是一家以开发软件以及网站为主的公司。现特推出大转盘抽奖活动回馈新老客户！诚信为本、质量至上是我们的宗旨，让优秀的品质、合理的价格、完善的服务见证我们的专业和真诚，期待与您更多商务合作！" wrap="soft" class="right clear">
					</textarea>
				</li>
				<li class="activity-rule clear">
					<label for="">活动规则:</label>
					<textarea name="activity-rule" placeholder="活动时间：xxxx年xx月xx日——xxxx年xx月xx日活动规则：每个微信号可抽一次，每个微信号可获得1次奖励。
奖品说明：
一等奖3名：xx品牌玩具
二等奖5名：xx拉杆箱
三等奖5名：xxx
本次活动最终解释权归xxx所有
" class="right clear">
					</textarea>
				</li>
			</ul>
			<div class="submit-btn">保存并继续设置</div>
		</section>
	</div>
</template>
<script type="text/javascript">
	export default {
		data(){
			return {}
		},
		mounted(){
				
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../../style/mixin';
	#baseSetting-main{
		.activity-type{
			h4{
				@include sc(0.7rem,#929292);
				padding-left:0.5rem;
				height:1.8rem;
				line-height:1.8rem;
			}
			ul.type-radio{
				background:#fff;
				padding:0 0.5rem;
				li{
					width:25%;
					height:2rem;
					line-height: 2rem;
					input{
						position:relative;
					}
					input:before{
						display: block;
					    width: 0.7rem;
					    height: 0.7rem;
					    content: '';
					    position: absolute;
					    top: -0.12rem;
					    left: -0.1rem;
					    background: #fff;
					    border: 1px solid #929292;
					    text-align: center;
					    line-height: 0.52rem;
					    font-size: 1.4rem;
					    border-radius: 50%;
					}
					input:checked:before{
						display: block;
					    width: 0.7rem;
					    height: 0.7rem;
					    content: '\2022';
					    color: #F35353;
					    position: absolute;
					    top: -0.12rem;
					    left: -0.1rem;
					    background: #fff;
					    border: 1px solid;
					    text-align: center;
					    line-height: 0.51rem;
					    font-size: 1.4rem;
					    border-radius: 50%;
					}
					label{
					@include sc(0.7rem,#333);
				}
				}
				
			}
		}
		.activity-base{
			h4{
				@include sc(0.7rem,#929292);
				padding-left:0.5rem;
				height:1.8rem;
				line-height:1.8rem;
			}
			.base-container{
				background:#fff;
				li.activity-name,li.activity-begin,li.activity-end{
					height:2rem;
					line-height: 1.8rem;
					padding:0 0.5rem;
					border-bottom:1px solid #D9D9D9;
					position:relative;
					label{
						@include sc(0.7rem,#929292);
					}
					input{
						display: block;
						width:11.1rem;
						@include sc(0.7rem,#929292);
						@include ct;
						right:0.5rem;
						&::placeholder{
							@include sc(0.7rem,#929292);
						}
					}
				}
				li.activity-introduce,li.activity-rule{
					padding:0 0.5rem;
					border-bottom:1px solid #D9D9D9;
					label{
						@include sc(0.7rem,#929292);
					}
					textarea{
						width:11.1rem;
						height:7.5rem;
						margin:0.3rem 0 0.5rem;
						text-align: left;
						&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
						  	@include sc(0.7rem,#929292);
						}
						&::-moz-placeholder { /* Firefox 19+ */
						  	@include sc(0.7rem,#929292);
						}
						&:-ms-input-placeholder { /* IE 10+ */
						 	@include sc(0.7rem,#929292);
						}
						&:-moz-placeholder { /* Firefox 18- */
						  	@include sc(0.7rem,#929292);
						}
					}
				}
				li.activity-rule textarea{
					height:8.5rem;
				}
			}
			.submit-btn{
				width: 90%;
			    height: 2rem;
			    line-height: 2rem;
			    background: #f35353;
			    text-align: center;
			    @include sc(0.7rem,#fff);
			    @include borderRadius(0.1rem);
			    margin: 0 auto;
			    margin-top: 1rem;
			}
		}
	}
</style>


